import {
  OBJECT_LIST_REQUEST,
  OBJECT_LIST_FAIL,
  OBJECT_LIST_SUCCESS
} from '../constants/ObjectList';

const initialState = {
  objListData: null
}

export default function object(state = initialState, action) {
  switch (action.type) {
    case OBJECT_LIST_REQUEST:
      return {...state};//;
    case OBJECT_LIST_SUCCESS:
      return {...state, objListData: action.payload.data};//
    case OBJECT_LIST_FAIL:
      return state;
    default:
      return state;
  }
}






