import {
  OBJECT_LIST_REQUEST,
  OBJECT_LIST_FAIL,
  OBJECT_LIST_SUCCESS
} from '../constants/ObjectList';
import ServerIo from './../server/ServerIo';

export function loadObjectList(payload,token) {

  return (dispatch) => {
    dispatch({
      type: OBJECT_LIST_REQUEST
    })
    var serverIo = new ServerIo(token);
    serverIo.getObjList({contentCode: payload.contentCode,limit: payload.limit,offset: payload.offset,sortColumn: payload.sortColumn,sortDirection: payload.sortDirection}).done(data=> {
      dispatch({
        type: OBJECT_LIST_SUCCESS,
        payload: {
          data: data
        }
      })

    }).fail(()=> {
      dispatch({
        type: OBJECT_LIST_FAIL,
        payload: {}
      })
    });
  }
}



