import React from "react";
import {Link} from "react-router";

class NotFound extends React.Component {

  render() {
    return (
      <div className="abs-center wd-xl">
        <div className="text-center mb-xl">
          <div className="text-lg mb-lg">404</div>
          <p className="lead m0">Нет страницы</p>
        </div>
        <div className="input-group mb-xl">
          <input type="text" placeholder="Try with a search" className="form-control"/>
                    <span className="input-group-btn">
                     <button type="button" className="btn btn-default">
                        <em className="fa fa-search"></em>
                     </button>
                  </span>
        </div>
        <ul className="list-inline text-center text-sm mb-xl">
          <li><Link to="dashboard" className="text-muted">На главную страницу</Link></li>
          <li className="text-muted">|</li>
          <li><Link to="login" className="text-muted">Авторизация</Link></li>
          <li className="text-muted">|</li>
        </ul>
      </div>
    );
  }

}

export default NotFound;




