import React from "react";
import {Route, IndexRoute} from "react-router";
import Base from "./components/Layout/Base";
import ObjList from "./components/objlist";
import Obj from './components/obj';
import BasePage from "./components/Layout/BasePage";
import SingleView from "./components/SingleView/SingleView";
import Login from "./components/Pages/Login";
import Recover from "./components/Pages/Recover";
import Lock from "./components/Pages/Lock";
import LogoutPage from "./components/Pages/LogoutPage";
import NotFound from "./components/Pages/NotFound";
import Error500 from "./components/Pages/Error500";
import Maintenance from "./components/Pages/Maintenance";
//import Register from "./components/Pages/Register";
import requireAuthentication from './containers/AuthenticatedComponent/index'
import WorkFlowDesogner from './components/workflowdesigner';

export const routes = (
  <div>
    <Route path="/" component={requireAuthentication(Base)}>


      {/* Default route*/}
      <IndexRoute component={SingleView}/>

      <Route path="singleview" component={SingleView}/>
      <Route path="objlist/:contentCode/page/:page" component={ObjList}/>
      <Route path='obj/:contentCode/:obj' name='obj' component={Obj}/>
      <Route path='new/:contentCode/:backLinkAttr/:objId'  name='newObj'  component={Obj}/>
      <Route path='workflowdesign' component={WorkFlowDesogner}/>
    </Route>


    {/*Pages*/}
    <Route path="/" component={BasePage}>
      <Route path="login" component={Login}/>
      <Route path="logout" component={LogoutPage}/>
      <Route path="recover" component={Recover}/>
      <Route path="lock" component={Lock}/>
      <Route path="notfound" component={NotFound}/>
      <Route path="error500" component={Error500}/>
      <Route path="maintenance" component={Maintenance}/>
    </Route>


    {/* Not found handler */}
    <Route path="*" component={NotFound}/>
  </div>
)



